import React from 'react'
import { NavLink } from 'react-router-dom'
import './Navbar.css'

const Navbar = () => {
  return (
    <div>


 {/* <!-- Navbar Start --> */}
    <div className="container-fluid nav-bar p-0">
        <div className="container-lg p-0">
            <nav className="navbar navbar-expand-lg bg-secondary navbar-dark">
                <NavLink to="/" className="navbar-brand">
                    <h1 className="m-0 text-white display-4"><span className="text-primary">W</span>EB<span className="text-primary">D</span>EV</h1>
                </NavLink>
                <button type="button" className="navbar-toggler" data-toggle="collapse" data-target="#navbarCollapse">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse justify-content-between" id="navbarCollapse">
                    <div className="navbar-nav ml-auto py-0">
                        <NavLink to="/" className="nav-item nav-link active">HOME</NavLink>
                        <NavLink to="/about" className="nav-item nav-link">ABOUT ME</NavLink>
                        <NavLink to="/services" className="nav-item nav-link">SERVICES</NavLink>
                        <NavLink to="/pricing" className="nav-item nav-link">ORDER NOW</NavLink>
                        <NavLink to="/contact" className="nav-item nav-link">CONTACT ME</NavLink>
                    </div>
                </div>
            </nav>
        </div>
    </div>


    </div>
  )
}

export default Navbar